.img-container {
  margin-left: 68px;
  width: 970px;
  height: 600px;
  position: relative;
}

.img-container .container-post {
  font-family: "Roboto Black";
  font-size: 20px;
  overflow-y: auto;
  color: #fff;
  margin: auto;
  white-space: initial;
  text-align: center;
  background: #000;
  transition: opacity 0.4s linear;
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
}

.text-post {
  margin-left: 20px;
  width: 900px;
}

.img-container:hover .container-post {
  opacity: 0.75;
}

.image-post {
  width: 970px;
  height: 600px;
}


.img-container-movil {
  display: none;
}


@media screen and (max-width: 720px) {
  .img-container {
    display: none;
  }

  .container-post {
    display: none;
  }

  .text-post {
    display: none;
  }

  .image-post {
    display: none;
  }

  .img-container-movil {
    margin-top: 10px;
    display: inherit;
    width: 80vw;
    height: 80vh;
    position: relative;
  }

  .image-post-movil {
    width: 100vw;
  }

  .text-post-movil {
    font-family: "Roboto Regular";
    text-align: center;
    width: 100vw;
    height: 50vh;
    overflow-y: auto;
  }
}
